<template>
  <g>
    <path :d="pathString" stroke-dasharray="15,10" class="path" :style="{stroke: lineColor}" />
  </g>
</template>

<script>
  export default {
    components: {},
    props: {
      route:{
        type: Array
      },
      index:{
        type: Number
      }
    },
    data () {
      return {
        colorlist:['#ee0000','#00ee00','#0000ee','#eeee00','#00eeee','#ee00ee','#0073ee','#73ee00','#7300ee','#ee7300']
      }
    },
    mounted(){

    },
    watch:{
    },
    computed:{
      pathString() {
        let path_str = 'M '+this.route[0].x*100+','+((-this.route[0].y)*100);
        path_str += ' L'+this.route[1].x*100+','+((-this.route[1].y)*100);
        for(let i=2;i<this.route.length;i++) {
          path_str += ' L'+this.route[i].x*100+','+((-this.route[i].y)*100);
        }
        console.log(path_str);
        return path_str;
      },
      lineColor() {
        let i = this.index % 10;
        return this.colorlist[i];
      }
    },
    methods: {
    }
  }
</script>

<style scoped>
.path{
  fill:none;stroke-width:6px;opacity:0.5;
}
</style>