<template>
  <div class="vehicles">
    <div class="action-area">
      <el-checkbox v-model="checked" style="color: #cccccc"></el-checkbox>
      <el-divider direction="vertical" />
      <el-button type="danger" size="mini" @click="pauseAll(true)"></el-button>
      <el-button type="success" size="mini" @click="pauseAll(false)"></el-button>
      <el-divider direction="vertical" />
      <el-button type="danger" size="mini" @click="pauseSelect(true)"></el-button>
      <el-button type="success" size="mini" @click="pauseSelect(false)"></el-button>
      <el-divider direction="vertical" />
      <el-button type="primary" size="mini" @click="withdrawSelect"></el-button>
      <el-divider direction="vertical" />
    </div>
    <el-divider/>
    <div class="bot-list">
      <bot-item
        v-for="(bot, index) in bots"
        :key="bot.id"
        v-bind="bot"
        :selected="selectedMap.get(bot.id)"
        @dblclick.native="greet(index)"
        @selectClick="select(index)"/>
    </div>
    <el-dialog :visible.sync="centerDialogVisible" :title="bots[selectedIndex].vehicle_id" center width="30%">
      <bot-item-extended v-bind="bots[selectedIndex]" />
    </el-dialog>
  </div>
</template>

<script>
import BotItem from "../components/BotItem.vue";
import BotItemExtended from "../components/BotExtended.vue";

export default {
  name: "Vehicles",
  data() {
    return {
      checked: false,
      centerDialogVisible: false,
      selectedIndex: 0,
      currentBot: {},
      bots: [],
      selectedMap: new Map(),
    };
  },
  watch: {
    checked(newVal) {
      this.selectedMap.forEach((selected, id) => {
        this.selectedMap.set(id, newVal);
      });
    },
  },
  beforeDestroy() {
    console.log('beforeDestroy');
    //clearTimeout(this.timer);
    this.$socket.emit('vehicleData', {getDate:false});
  },
  mounted() {
    this.$socket.emit('vehicleData', {getDate:true});
  },
  sockets: {
    vehicleData(data) {
      console.log(data);
      this.bots = data;
      let tempMap = new Map();
      for (let i = 0; i < this.bots.length; i++) {
        tempMap.set(this.bots[i].id, true);
        if (!this.selectedMap.has(this.bots[i].id)) {
          this.selectedMap.set(this.bots[i].id, false);
        }
      }
      this.selectedMap.forEach((selected, id) => {
        if (!tempMap.get(id)) {
          this.selectedMap.delete(id);
        }
      });
      console.log(this.selectedMap);
    }
  },
  methods: {
    /*fetchData() {
      this.$axios
        .get("/api/route/vehicles")
        .then((res) => {
          console.log(res.data);
          this.bots = res.data;
          let tempMap = new Map();
          for (let i = 0; i < this.bots.length; i++) {
            tempMap.set(this.bots[i].id, true);
            if (!this.selectedMap.has(this.bots[i].id)) {
              this.selectedMap.set(this.bots[i].id, false);
            }
          }
          this.selectedMap.forEach((selected, id) => {
            if (!tempMap.get(id)) {
              this.selectedMap.delete(id);
            }
          });
          console.log(this.selectedMap);
        })
        .catch((err) => {
          console.log("err=>", err);
        });
      this.timer = setTimeout(()=>{this.fetchData();},2000);
    },*/
    pauseAll(bPause) {
      this.$axios.post("/api/route/vehicles/pause",{},{params: {newValue:bPause}});
    },
    pauseSelect(bPause) {
      this.bots.forEach((vehicle)=>{
        if(this.selectedMap.get(vehicle.id) == true) {
          this.$axios.post("/api/route/vehicles/"+vehicle.vehicle_id+"/pause",{},{params: {newValue:bPause}});
        }
      });
    },
    withdrawSelect() {
      this.bots.forEach((vehicle)=>{
        if(this.selectedMap.get(vehicle.id) == true) {
          this.$axios.post("/api/route/vehicles/"+vehicle.vehicle_id+"/withdrawal",{},{params: {immediate:false, disableVehicle:false}});
        }
      });
    },

    getSelected(index){
      let id = this.bots[index].id;
      return this.selectedMap.get(id);
    },
    cancelAutoUpdate() {
      console.log("cancelAutoUpdate");
      clearTimeout(this.timer);
    },
    greet(index) {
      console.log("greet");
      this.centerDialogVisible = true;
      this.currentBot = this.bots[index];
    },
    select(index) {
      console.log("select "+index);
      let id = this.bots[index].id;
      this.selectedMap.set(id, !this.selectedMap.get(id));
      this.$set(this.bots, index, this.bots[index]);
    },
    
    /*sendrequest() {
      console.log("hh");
      let selectedList = [];
      this.bots.forEach((bot) => {
        if (bot.selected) {
          selectedList.push(bot.id);
        }
      });
      console.log(selectedList);
    },*/
  },

  components: {
    BotItem,
    BotItemExtended,
  },
};
</script>

<style scoped>
.action-area {
  display: flex;
  align-items: center;
  margin-top: 20px;
  margin-left: 12px;
}

.bot-list {
  display: flex;
  flex-wrap: wrap;
  margin-left: 8px;
