<template>
  <div class="bot_item">
    <div class="bot_item_title">
      <el-checkbox :value="self_selected" @change="$emit('selectClick')"/>
      <span style="margin-left: 15px; font-size: 16px">{{ vehicle_id }}</span>
    </div>

    <table class="info_area">
      <tr style="height: 24px">
        <td width="35%"></td>
        <td width="65%">
          <el-progress
            :percentage="parseInt(battery_level * 100)"
            :stroke-width="16"
            :color="batteryColors"
            :text-inside="true"
          />
        </td>
      </tr>
      <tr style="height: 24px">
        <td width="35%"></td>
        <td width="65%">{{ onlineStatus }}</td>
      </tr>
      <tr style="height: 24px">
        <td width="35%"></td>
        <td width="65%">{{ taskStatus }}</td>
      </tr>
      <tr style="height: 24px">
        <td width="35%"></td>
        <td width="65%">{{ current_station }}</td>
      </tr>
      <tr style="height: 24px">
        <td width="35%"></td>
        <td width="65%">{{ current_order }}</td>
      </tr>
      <tr style="height: 24px">
        <td width="35%"></td>
        <td width="65%">{{ current_map }}</td>
      </tr>
    </table>
  </div>
</template>

<script>
export default {
  components: {},
  props: {
    status: {
      type: String,
      default: "offline",
    },
    vehicle_id: {
      type: String,
      default: "",
    },
    current_station: {
      type: String,
      default: "",
    },
    task_status: {
      type: Number,
      default: 0,
    },
    current_map: {
      type: String,
      default: "",
    },
    battery_level: {
      type: Number,
      default: 0.8,
    },
    current_order: {
      type: String,
      default: "",
    },
    selected: {
      type: Boolean,
      default: false,
    },
  },
  data() {
    return {
      self_selected: false,
    };
  },
  watch: {
    selected(newVal) {
      this.self_selected = newVal;
    },
  },

  computed: {
    batteryColors() {
      return this.battery_level > 0.5
        ? "#67C23A"
        : this.battery_level > 0.2
        ? "#E6A23C"
        : "#F56C6C";
    },
    onlineStatus() {
      if (this.status == "connected") {
        return "";
      } else if (this.status == "offline") {
        return "";
      } else {
        return "";
      }
    },
    taskStatus() {
      if (this.task_status == 0) {
        return "";
      } else if (this.task_status == 2) {
        return "";
      } else if (this.task_status == 3) {
        return "";
      } else if (this.task_status == 4) {
        return "";
      } else {
        return "";
      }
    },
  },
  methods: {
    selectClick: function () {
      console.log("selectClick");
    },
  },
};
</script>

