<template>
  <g :transform="'translate('+x*100+','+((-y)*100)+')'" @click.stop="$emit('select')">
    <g :transform="botRotate">
      <rect :x="-botLength*50" :y="-botWidth*50" :width="botLength*100" :height="botWidth*100" class="bot_shape" :style="{stroke: color, strokeWidth: strokeWidth}"/>
      <path fill="#eeeeee" stroke="#eeeeee" :d="arrowPath"/>
    </g>
    <!--text style="dominant-baseline:middle;text-anchor:middle;" fill="#666666" font-size="20">{{vehicle_id}}</text-->
    <!--circle :r="3" fill="red"/-->
    <title>{{vehicle_id}}</title>
  </g>
</template>

<script>
  export default {
    components: {},
    props: {
      vehicle_id:{
        type: String,
        default: ""
      },
      x:{
        type: Number,
        default: 0
      },
      y:{
        type: Number,
        default: 0
      },
      angle:{
        type: Number,
        default: 0
      },
      botWidth:{
        type: Number,
        default: 0.4
      },
      botLength:{
        type: Number,
        default: 0.6
      },
      task_status:{
        type: Number,
        default:0
      },
      active:{
        type: Boolean,
        default: false
      },
      index:{
        type: Number
      }
    },
    data () {
      return {
        colorlist:['#ee0000','#00ee00','#0000ee','#eeee00','#00eeee','#ee00ee','#0073ee','#73ee00','#7300ee','#ee7300']
      }
    },
    computed:{
      botRotate: function() {
        return 'rotate('+(-this.angle)*180/Math.PI+')';
      },
      arrowPath: function() {
        return 'M'+(this.botLength*50-4)+',0 l-12,-7 l0,14 z';
      },
      color() {
        if(this.task_status == 2) {
          let i = this.index % 10;
          return this.colorlist[i];
        } else {
          return '#3366ff';
        }
      },
      strokeWidth() {
        if(this.task_status == 2) {
          return '3px'
        } else {
          return '1px';
        }
      }
    },
    methods: {
      test() {
        console.log("test");
      }
    }
  }
</script>

<style scoped>
.bot_shape{
  stroke: #3366ff; stroke-width: 1px;fill:#6699ff;fill-opacity:0.8;
}
.bot_shape_active{
  stroke: #F7B902; stroke-width: 3px;fill:#6699ff; fill-opacity:0.8;
}
</style>