<template>
  <div id="map_page">
    <div id="action_bar">
      <span>{{header.mapName}}</span>
      <el-divider direction="vertical" />
      <el-button type="primary" size="mini" style="margin-left:6px;" @click="mapManage"></el-button>
      <el-divider direction="vertical" />
      <el-upload action="http://localhost:30001/api/route/maps/test" name="map" :show-file-list="false" :on-success="uploadSuccess" :limit="1" accept=".smap">
        <el-button type="primary" size="mini"></el-button>
      </el-upload>
      <el-divider direction="vertical" />
       <el-button type="primary" size="mini" style="margin-left:6px;" @click="showFlow"></el-button>
    </div>
    <el-divider/>
    <div id="map_view">
      <svg id="element_svg">
        <g :transform="'translate('+offsetX+','+(offsetY+(minYInMap+maxYInMap)*scale)+')scale('+scale/100+')'">
          <g>
            <path-line v-for="(line,index) in pathList" :key="line.instanceName+index" v-bind="line" @select="selectLine(index)"/>
          </g>
          <g>
            <station v-for="(item,index) in advancedPointList" :key="item.instanceName" v-bind="item" @select="selectStation(index)"/>
          </g>
          <g>
            <route v-for="item in routeList" :key="item.id" :route="item.route" :index="item.id"/>
          </g>
          <g v-show="showAGV">
            <robot v-for="(bot,index) in botList" :key="bot.id" v-bind="bot" :index="index"/>
          </g>
        </g>
      </svg>
    </div>
    <el-dialog title="" :visible.sync="showMapManage" >
      <el-table :data="mapList" size="mini" :border="true"
        :cell-style="{textAlign: 'center'}" 
        :header-cell-style="{textAlign: 'center'}">
        <el-table-column label="">
          <template slot-scope="scope">
            <span v-if="current_filename == scope.row.filename"></span>
            <el-button type="text" size="mini" @click="setMap(scope.$index, scope.row)" v-else></el-button>
          </template>
        </el-table-column>
        <el-table-column property="originalname" label=""></el-table-column>
        <el-table-column property="owner" label="" width="150"></el-table-column>
        <el-table-column property="create_time" label="" width="150"></el-table-column>
      </el-table>
    </el-dialog>
    <el-dialog :visible.sync="showInfo" :modal-append-to-body="false" :center="true" width="300px" top="25vh">
      <div v-for="item in info" :key="item.key" style="display:flex;align-items: center;margin-top:4px;">
        <span style="width:80px;padding-left:10px;">{{item.key}}</span>
        <span style="padding-left:8px;background:#eeeeee;flex:1;height:30px;line-height:30px;display:block;">{{item.value}}</span>
      </div>
    </el-dialog>
  </div>
</template>

<script>
  import Station from './Station';
  import PathLine from './PathLine';
  import Robot from './Robot';
  import Route from './Route';
  export default {
    components: {Station, PathLine, Robot, Route},
    props: {
    },
    data () {
      return {
        botList:[],
        routeList:[],
        mapList:[],
        advancedPointList:[],
        stationMap: new Map(),
        advancedCurveList:[],
        pathList:[],
        header:{},
        smap:{},
        scale: 10,
        offsetX: 0,
        offsetY: 0,
        minYInMap:0,
        maxYInMap:0,
        pictureUrl: '/img/',
        showAGV: true,
        showMapManage: false,
        showInfo: false,
        info:[],
        selectedLineindex : -1,
        selectedStationindex :-1,
        current_filename: "",
        bShowFlow: false
      }
    },
    watch: {
      header(newVal) {
        console.log('header change');
        this.maxWidthInMap = newVal.maxPos.x - newVal.minPos.x;
        this.maxHeightInMap = newVal.maxPos.y - newVal.minPos.y;
        this.minYInMap = newVal.minPos.y;
        this.maxYInMap = newVal.maxPos.y;
        this.updateScale();
        this.offsetX = Math.round(-newVal.minPos.x * this.scale);
        this.offsetY = Math.round(-newVal.minPos.y * this.scale);
      }
    },
    computed:{
    },
    created(){

    },
      
    beforeDestroy() {
      console.log('beforeDestroy');
      this.$socket.emit('vehicleData', {getDate:false});
      this.$socket.emit('RouteData', {getDate:false});
      if(this.bShowFlow) {
        this.$socket.emit('FlowData', {getDate:false});
      }
    },
    mounted(){
      this.getCurrentMap();
      this.$socket.emit('vehicleData', {getDate:true});
      this.$socket.emit('RouteData', {getDate:true});
      //this.$socket.emit('FlowData', {getDate:true});


      this.mapview = document.getElementById("map_view");
      this.svg = document.getElementById("element_svg");
      this.svg.onmousewheel = this.mouseWheel;

      this.curveMap = new Map();

      this.svg.onmousedown = e => {
        if(e.buttons == 1) {
            this.moved = false;
            this.svg.onmousemove = e => {
              this.moved = true;
              this.offsetX += e.movementX;
              this.offsetY += e.movementY;
             // this.drawMap();
            };
            this.svg.onmouseup = () => {
              this.svg.onmousemove = null;
              this.svg.onmouseup = null;
            };
        }
      }
    },
    sockets: {
      vehicleData(data) {
        console.log(data);
        this.botList = [];
        data.forEach((bot)=>{
          if(bot.status == 'connected') {
            this.botList.push(bot);
          }
        });
      },
      RouteData(data) {
        console.log(data);
        this.routeList = [];
        for(let i=0; i<data.length; i++){
          //console.log(data[i]);
          let route = [];
          let index = 0;
          for(let k=0;k<this.botList.length;k++) {
            if(this.botList[k].vehicle_id == data[i].vehicle_id) {
              index = k;
              break;
            }
          }
          let item = {id: index, route:route};
          route.push(this.stationMap.get(data[i].route[0].start));
          route.push(this.stationMap.get(data[i].route[0].end));
          for(let j=1;j<data[i].route.length;j++) {
            route.push(this.stationMap.get(data[i].route[j].end));
          }
          this.routeList.push(item);
        }
        console.log(this.routeList);
      },
      FlowData(data) {
        console.log(data);
        data.forEach(res => {
          for (let i=0;i<this.pathList.length;i++){
            if(this.pathList[i].instanceName == res.instanceName) {
              this.pathList[i].flow = res.flow;
              this.$set(this.pathList, i, this.pathList[i]);
            }
          }
        });
        console.log(this.pathList);
      }
    },
    methods: {
      getCurrentMap() {
        this.$axios.get('/api/route/maps/current')
        .then(res=>{
          this.advancedPointList=res.data.advancedPointList;
          this.advancedCurveList=res.data.advancedCurveList;
          this.getPath(this.advancedCurveList);
          this.header = res.data.header;
          this.stationMap.clear();
          this.advancedPointList.forEach((station)=>{
            this.stationMap.set(station.instanceName, station.pos);
          });
          console.log(this.stationMap);
        })
        .catch(err=>{
          console.log('err=>',err);
        });
      },
      getPath(curveList) {
        //this.curveMap.clear();
        let curveMap = new Map();
        this.pathList = [];
        curveList.forEach((curve)=>{
          let reverseName = curve.endPos.instanceName + '-' + curve.startPos.instanceName;
          let reverseCurve = curveMap.get(reverseName);
          if (reverseCurve) {
            reverseCurve.twoWay = true;
          } else {
            curveMap.set(curve.instanceName, curve);
          }
        });
        curveMap.forEach((curve)=>{
          this.pathList.push(curve);
        })
        console.log(this.pathList);
      },
      uploadSuccess() {
        this.$message({
          message: '',
          type: 'success'
        });
      },
      setMap(index, row) {
        console.log(index);
        console.log(row);
        this.$axios.put("/api/route/maps/current",{filename:row.filename})
          .then(()=>{
            this.mapManage();
            this.getCurrentMap();
          })
      },

      mapManage() {
        this.$axios.get("/api/route/maps/current/filename")
          .then((res)=>{
            console.log(res.data);
            this.current_filename = res.data;
          })
          .catch((err) => {
            console.log("err=>", err);
          });
        this.$axios.get("/api/route/maps")
          .then((res)=>{
            console.log(res.data);
            this.mapList = res.data;
            this.showMapManage = true;
          })
          .catch((err) => {
            console.log("err=>", err);
          });
      },

      /*getVehicles() {
        this.$axios
          .get("http://localhost:30001/api/route/vehicles")
          .then((res) => {
            console.log(res.data);
            this.botList = [];
            res.data.forEach((bot)=>{
              if(bot.status == 'connected') {
                this.botList.push(bot);
              }
            });
          })
          .catch((err) => {
            console.log("err=>", err);
          });
        this.vehicleTimer = setTimeout(()=>{this.getVehicles();},1000);
      },*/

      /*getProcessRoute() {
        this.$axios
          .get("http://localhost:30001/api/route/maps/currentDriveOrder")
          .then((res) => {
            console.log(res.data);
            //this.botList = res.data;
            this.routeList = [];
            for(let i=0; i<res.data.length; i++){
              console.log(res.data[i]);
              let route = [];
              let item = {id: i, route:route};
              route.push(this.stationMap.get(res.data[i][0].start));
              route.push(this.stationMap.get(res.data[i][0].end));
              for(let j=1;j<res.data[i].length;j++) {
                route.push(this.stationMap.get(res.data[i][j].end));
              }
              this.routeList.push(item);
            }
            console.log(this.routeList);
          })
          .catch((err) => {
            console.log("err=>", err);
          });
        this.routeTimer = setTimeout(()=>{this.getProcessRoute();},1000);
      },*/
      mouseWheel(event) {
        //if (event.ctrlKey) {
          if(event.wheelDelta > 0) {
            this.scaleUp(event.offsetX, event.offsetY);
          } else {
            this.scaleDown(event.offsetX, event.offsetY);
          }
        //}
      },
      scaleUp(x,y) {
        if(this.scale == 400) {
          return;
        }
        let originalScale = this.scale;
        if(this.scale < 10) {
          this.scale += 2;
        } else if(this.scale < 20) {
          this.scale += 5;
        } else if(this.scale < 100) {
          this.scale += 10;
        } else if(this.scale < 200) {
          this.scale += 20;
        } else {
          this.scale += 50;
        }
        console.log(this.scale);
        let deltaScale = this.scale/originalScale;
        this.offsetX = x+deltaScale*(this.offsetX-x);
        this.offsetY = y+deltaScale*(this.offsetY-y);
        //this.drawMap();
      },
      scaleDown(x,y) {
        if(this.scale == 2) {
          return;
        }
        let originalScale = this.scale;
        if(this.scale > 200) {
          this.scale -= 50;
        } else if(this.scale > 100) {
          this.scale -= 20;
        } else if(this.scale > 20) {
          this.scale -= 10;
        } else if(this.scale > 10) {
          this.scale -= 5;
        } else {
          this.scale -= 2;
        }
        console.log(this.scale);
        let deltaScale = this.scale/originalScale;
        this.offsetX = x+deltaScale*(this.offsetX-x);
        this.offsetY = y+deltaScale*(this.offsetY-y);
        //this.drawMap();
      },
      updateScale() {
        console.log(this.mapview);
        var scaleWidth = this.mapview.clientWidth / this.maxWidthInMap;
        var scaleHeight = this.mapview.clientHeight / this.maxHeightInMap;
        console.log(scaleWidth);
        console.log(scaleHeight);
        let scale = Math.min(scaleWidth, scaleHeight);
        console.log(scale);
        if(scale > 10) {
          this.scale = Math.round(scale/10)*10;
        } else {
          this.scale = Math.round(scale/2)*2;
        }
        console.log('updateScale '+this.scale);
      },

      selectStation(index) {
        console.log(index);
        this.selectedStationindex = index;
        let info = [];
        let item = {key:'', value:this.advancedPointList[this.selectedStationindex].instanceName};
        info.push(item);
        item = {key:'X', value:this.advancedPointList[this.selectedStationindex].pos.x};
        info.push(item);
        item = {key:'Y', value:this.advancedPointList[this.selectedStationindex].pos.y};
        info.push(item);
        let angle_value = ''
        if(this.advancedPointList[this.selectedStationindex].dir) {
          let degree = this.advancedPointList[this.selectedStationindex].dir * 180 / Math.PI;
          angle_value = degree+""
        } else {
          angle_value = ''
        }
        item = {key:'', value:angle_value};
        info.push(item);
        this.info = info;
        this.showInfo = true;
      },

      selectLine(index) {
        console.log(index);
        //this.selectedLineindex = index;
        //this.showInfo = true;
      },

      showFlow() {
        this.bShowFlow = !this.bShowFlow;
        if(this.bShowFlow) {
          this.$socket.emit('FlowData', {getDate:true});
        } else {
          this.$socket.emit('FlowData', {getDate:false});
          for (let i=0;i<this.pathList.length;i++){
            this.pathList[i].flow = [];
            this.$set(this.pathList, i, this.pathList[i]);
          }
        }
      }
    }
  }
</script>

<style scoped>
#map_page {
  width: 100%;
  height: 100%;
  position: relative;
  display: flex;
  flex-direction: column;
  font-size: 15px;
}

#map_view {
  width: 100%;
  height: 100%;
  position: relative;
}

#element_svg{
width:98%;height:98%;
position:absolute;top:0;left:0;
margin-left:1%;
background:#576174;
}

#action_bar{
  margin-top:20px;
  margin-left:12px;
  display:flex;
  align-items: center;
}
</style>