<template>
  <div id="login_view">
    <div id="login_form">
      <p style="margin-top: 36px;font-size:24px;">CRCS </p>
      <el-input style="width: 250px;  margin-top: 10px" v-model="user" placeholder=""/>
      <el-input style="width: 250px; margin-top: 20px" v-model="password" placeholder="" show-password/>
      <el-button style="width: 160px; margin-top: 30px" type="primary" @click="login"></el-button>
      <span style="margin-top: 10px;color:#ee1111">{{msg}}</span>
    </div>
  </div>
</template>

<script>
export default {
  name: "Login",
  data() {
    return {
      user: '',
      password: '',
      msg: ''
    };
  },
  methods: {
    login() {
      //console.log("login : "+this.user+", "+this.password);
      /*sessionStorage.setItem('user', this.user);
      this.$router.push({
            name: 'Index'
          });*/
      this.msg = '';
      let data = {"user":this.user,"password":this.password};
      this.$axios.post('/api/route/users/login', data)
      .then(res=>{
        console.log(res.data);
        if(res.data.ret_code == 0) {
          sessionStorage.setItem('user', this.user);
          this.$router.push({
            name: 'Index'
          });
        } else {
          this.msg = res.data.msg;
        }
      })
      .catch(err=>{
        console.log('err=>',err);
      });
    }
  }
};
</script>


<style scoped>
#login_view {
  width: 100%;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
}

#login_form {
  width: 400px;
  height: 300px;
  border-radius: 10px;
  background-color: #525966;
  display: flex;
  flex-direction: column;
  align-items: center;
}
</style>
