<template>
  <div>
    <div class="order_search_bar">
      <span></span><el-input style="width:160px;margin-left:4px;" size="mini" v-model="orderNameFilter"/>
      <span style="margin-left:10px;"></span><el-input style="width:100px;margin-left:4px;" size="mini" v-model="intendedVehicleFilter"/>
      <span style="margin-left:10px;"></span><el-input style="width:100px;margin-left:4px;" size="mini" v-model="processingVehicleFilter"/>
      <span style="margin-left:10px;"></span>
      <el-select v-model="stateFilter" placeholder="" clearable size="mini" style="width:150px;margin-left:4px;">
        <el-option
          v-for="item in stateOptions"
          :key="item"
          :label="item"
          :value="item">
        </el-option>
      </el-select>
      <span style="margin-left:10px;"></span>
      <el-select v-model="categoryFilter" placeholder="" clearable size="mini" style="width:100px;margin-left:4px;">
        <el-option
          v-for="item in categoryOptions"
          :key="item"
          :label="item"
          :value="item">
        </el-option>
      </el-select>

      <el-button type="primary" size="mini" @click="searchOrder" style="margin-left:10px;"></el-button>
    </div>
    <el-divider/>
    <el-table :data="orderList" style="width: 98%;margin-left:1%;"  
      :cell-style="{background: '#373E4B',color: '#ffffff',textAlign: 'center',fontSize:'10px'}" 
      :header-cell-style="{background: '#424a58',color: '#ffffff',textAlign: 'center'}"
      :border="true"
      size="mini" ref="orderTable"
      @selection-change="handleSelectionChange" >
      <el-table-column type="selection" width="50"> </el-table-column>
      <el-table-column prop="name"  label=""> </el-table-column>
      <el-table-column prop="route" label=""> </el-table-column>
      <el-table-column prop="intendedVehicle" width="100" label=""></el-table-column>
      <el-table-column prop="processingVehicle" width="100" label=""></el-table-column>
      <el-table-column prop="creationTime" width="160" label=""></el-table-column>
      <el-table-column prop="state" width="150" label=""></el-table-column>
      <el-table-column prop="category" width="80" label="" ></el-table-column>
    </el-table>
    <div class="order_action_bar">
      <el-button type="primary" size="mini" @click="withdrawOrder" style="margin-left:10px;" :disabled="hasSelectedOrder"></el-button>
      <el-divider direction="vertical" />
      <el-button type="text" size="mini" @click="lastPage" :disabled="currentPage == 1"></el-button>
      <span style="margin-left:6px;"></span>
      <el-input type="number" :min="1" :max="totalPage" style="width:50px;margin-left:3px;margin-right:3px;" size="mini" v-model="currentPage" @change="currentPageChange"/>
      <span></span>
      <el-button type="text" size="mini" @click="nextPage" style="margin-left:6px;" :disabled="currentPage == totalPage"></el-button>
      <span style="margin-left:8px;"> {{totalPage}}  </span>
      <span style="margin-left:8px;"></span>
      <el-input type="number" :min="1" style="width:50px;margin-left:3px;margin-right:3px;" size="mini" v-model="pageSize" @change="pageSizeChange"/><span></span>
      <el-divider direction="vertical" />
      <el-button type="success" size="mini" @click="exportFile"></el-button>
    </div>
  </div>
</template>

<script>
  import ElTableExport from "el-table-export";

  export default {
    data () {
      return {
        stateList:[],
        orderNameFilter:"",
        intendedVehicleFilter:"",
        processingVehicleFilter:"",
        categoryFilter:"",
        stateFilter:"",
        orderList:[],
        stateOptions:['DISPATCHABLE','BEING_PROCESSED','WITHDRAWN','FINISHED','FAILED','UNROUTABLE'],
        categoryOptions:['Normal','Park','Charge'],
        selectedOrder:[],
        currentPage: 1,
        totalPage: 1,
        pageSize: 20
      };
    },
    mounted() {
      this.searchOrder();
    },
    computed:{
      hasSelectedOrder() {
        return this.selectedOrder.length>0 ? false : true;
      }
    },
    watch:{
      /*currentPage(oldVal, newVal) {
        if(newVal != oldVal) {
          this.searchOrder();
        }
      },*/
      /*pageSize(oldVal, newVal) {
        if(newVal != oldVal) {
          this.searchOrder();
        }
      }*/
    },
    methods: {
      handleSelectionChange(val) {
        this.selectedOrder = val;
      },
      pageSizeChange() {
        //console.log("pageSizeChange");
        //console.log(event);
        this.searchOrder();
      },
      currentPageChange() {
        this.searchOrder();
      },
      withdrawOrder() {
        let number = this.selectedOrder.length;
        this.selectedOrder.forEach((order)=>{
          this.$axios.post("/api/route/transportOrders/"+order.name+"/withdrawal")
          .then(() => {
            //console.log(res);
            number--;
            if(number == 0) {
              this.searchOrder();
            }
          })
          .catch((err) => {
            console.log("err=>", err);
          });
        })
      },
      lastPage() {
        this.currentPage--;
        this.searchOrder();
      },
      nextPage() {
        this.currentPage++;
        this.searchOrder();
      },
      searchOrder(){
        let orderParam = {};
        orderParam.pageNo = this.currentPage;
        orderParam.pageSize = this.pageSize;
        if(this.intendedVehicleFilter != "") {
          orderParam.intendedVehicle = this.intendedVehicleFilter;
        }
        if(this.processingVehicleFilter != "") {
          orderParam.processingVehicle = this.processingVehicleFilter;
        }
        if(this.categoryFilter != "") {
          orderParam.category = this.categoryFilter;
        }
        if(this.stateFilter != "") {
          orderParam.state = this.stateFilter;
        }
        this.$axios
        .get("/api/route/transportOrders",{params: orderParam})
        .then((res) => {
          console.log(res.data);
          this.totalPage = res.data.total;
          this.refreashList(res.data.page);
        })
        .catch((err) => {
          console.log("err=>", err);
        });
      },
      refreashList(data) {
        this.orderList = [];
        data.forEach((order)=>{
          let route = "";
          if(order.destinations.length > 0) {
            route = order.destinations[0].locationName;
          }
          for(let i=1;i<order.destinations.length;i++) {
            route += " -> "+order.destinations[i].locationName;
          }
          if(order.creationTime) {
            order.creationTime = new Date(order.creationTime).toLocaleString();
          }
          this.orderList.push(Object.assign({}, order, { route: route }));
        });
      },
      exportFile() {
        ElTableExport(this.$refs.orderTable, {
          fileName: "export-demo-" + new Date().getTime(),
          type: "xls"
        })
        .then(() => {
          console.log("ok");
        })
        .catch((err) => {
          console.log(err);
        });
      }
    }
  }
</script>

<style>
.order_search_bar{
  display:flex;
  align-items: center;
  font-size: 14px;
  min-width: 1100px;
  margin-left:12px;
  margin-top: 20px;
}

.order_action_bar{
  margin-top:20px;
  display:flex;
  align-items: center;
}

.el-input__inner { 
  padding-right: 1px;
}

</style>