<template>
  <div class="bot_item_extended">
    <table class="info_area">
      <tr style="height: 24px">
        <td width="35%"></td>
        <td width="65%">
          <span v-if="status == 'online'">  </span>
          <span v-else-if="status == 'offline'">  </span>
          <span v-else-if="status == 'connected'">  </span>
        </td>
      </tr>
      <tr style="height: 24px">
        <td width="35%"></td>
        <td width="65%">
          <span v-if="task_status == 0">  </span>
          <span v-else-if="task_status == 2">  </span>
          <span v-else-if="task_status == 3">  </span>
          <span v-else-if="task_status == 4">  </span>
        </td>
      </tr>
      <tr style="height: 24px">
        <td width="35%"></td>
        <td width="65%">
          <span v-if="task_status == 3">  </span>
          <span v-else>  </span>
        </td>
      </tr>
      <tr style="height: 24px">
        <td width="35%"></td>
        <td width="65%">
          {{battery_level*100}}%
        </td>
      </tr>
      <tr style="height: 24px">
        <td width="35%"></td>
        <td width="65%">{{ current_station }}</td>
      </tr>
      <tr style="height: 24px">
        <td width="35%"></td>
        <td width="65%"></td>
      </tr>
      <tr style="height: 24px">
        <td width="35%"></td>
        <td width="65%">
          {{ x.toFixed(3) }} , {{ y.toFixed(3) }}
        </td>
      </tr>
      <tr style="height: 24px">
        <td width="35%">()</td>
        <td width="65%">{{ (angle*180/Math.PI).toFixed(3) }}</td>
      </tr>
      <tr style="height: 24px">
        <td width="35%"></td>
        <td width="65%">
          {{ confidence.toFixed(3) }}
        </td>
      </tr>
      <tr style="height: 24px">
        <td width="35%">(rad/s)</td>
        <td width="65%">{{ w.toFixed(3) }}</td>
      </tr>
      <tr style="height: 24px">
        <td width="35%"></td>
        <td width="65%">
          <span v-if="blocked == true">  </span>
          <span v-else>  </span>
        </td>
      </tr>

      <tr style="height: 24px">
        <td width="35%">(C)</td>
        <td width="65%">{{ battery_temp }}</td>
      </tr>
      <tr style="height: 24px">
        <td width="35%">(V)</td>
        <td width="65%">{{ voltage }}</td>
      </tr>
      <tr style="height: 24px">
        <td width="35%"></td>
        <td width="65%">
          <span v-if="charging == true">  </span>
          <span v-else>  </span>
        </td>
      </tr>
      <tr style="height: 24px">
        <td width="35%"></td>
        <td width="65%">
          <span v-if="emergency == true">  </span>
          <span v-else>  </span>
        </td>
      </tr>
      <tr style="height: 24px">
        <td width="35%"></td>
        <td width="65%"></td>
      </tr>
    </table>
  </div>
</template>

<script>
export default {
  components: {},
  props: {
    status: {
      type: String,
      default: "offline",
    },
    current_status: {
      type: String,
      default: "",
    },
    task_status: {
      type: Number,
      default: 0,
    },
    battery_level: {
      type: Number,
      default: 0,
    },
    current_station: {
      type: String,
      default: "",
    },
    waybill: {
      type: String,
      default: "",
    },
    x: {
      type: Number,
      default: 0,
    },
    y: {
      type: Number,
      default: 0,
    },
    angle: {
      type: Number,
      default: 0,
    },
    confidence: {
      type: Number,
      default: 0,
    },
    w: {
      type: Number,
      default: 0,
    },
    blocked: {
      type: Boolean,
      default: false,
    },
    battery_temp: {
      type: Number,
      default: 0,
    },
    voltage: {
      type: Number,
      default: 0,
    },
    charging: {
      type: Boolean,
      default: false,
    },
    emergency: {
      type: Boolean,
      default: false,
    },
    current_map: {
      type: String,
      default: "",
    },

    current_ip: {
      type: String,
      default: "0.0.0.0",
    },
    vehicle_id: {
      type: String,
      default: "",
    },
    MAC: {
      type: String,
      default: "",
    },

    port: {
      type: Number,
      default: 0,
    },
    id: {
      type: String,
      default: "",
    },
  },
  data() {
    return {
      selected: false,
    };
  },
  computed: {
    batteryColors() {
      return this.battery_level > 0.5
        ? "#67C23A"
        : this.battery_level > 0.2
        ? "#E6A23C"
        : "#F56C6C";
    },
  },
};
</script>

<style scoped>
.bot_item {
  width: 280px;
  height: 280px;
  display: flex;
  flex-direction: column;
  border: 2px solid #dcdfe6;
  padding: 2px;
  font-size: 14px;
  margin-top: 8px;
  margin-left: 4px;
}
.bot_item:hover {
  border: 2px solid #d29e02;
}
.bot_item_title {
  background: #fdfdfd;
  display: flex;
  align-items: center;
  height: 30px;
  padding-left: 6px;
}

.battery-level {
  font-size: 12px;
}

.connect_icon {
  width: 16px;
  height: 16px;
  border: 1px solid #ffffff;
  border-radius: 50%;
}
.connect_icon_connected {
  background: #0ae954;
}
.connect_icon_online {
  background: #f7b902;
}
.connect_icon_offline {
  background: #aaaaaa;
}

.setting_btn {
  align-self: flex-end;
}

.info_area {
  width: 100%;
  background: #ffffff;
}

.info_line {
  margin-bottom: 5px;
}

.action_area {
  height: 20px;
  display: flex;
  flex-direction: row-reverse;
}

.div.el-dialog__header {
  background: red !important;
}
</style>
