<template>
  <g>
    <path :d="pathString" :class="{path_active: active, path: !active}" @click.stop="$emit('select')"/>
    <rect v-show="active && className == 'BezierPath'" :x="controlPos1.x*100-5" :y="((-controlPos1.y)*100)-5" width="10" height="10" fill="#dd0000" :id="instanceName+'_1'"/>
    <rect v-show="active && className == 'BezierPath'" :x="controlPos2.x*100-5" :y="((-controlPos2.y)*100)-5" width="10" height="10" fill="#dd0000" :id="instanceName+'_2'"/>
    <path :d="arrowString" stroke="#dddddd" stroke-width="1" fill="#dddddd" :transform="'rotate('+arrowAngle+' '+centerX+','+centerY+')'"/>
    <text fill="#dddddd" font-size="10" :transform="'translate('+(centerX+5)+','+(centerY-5)+')'">{{flowText}}</text>
    <title>{{instanceName}}</title>
  </g>
</template>

<script>
  export default {
    components: {},
    props: {
      className:{
        type: String,
        default: ""
      },
      instanceName:{
        type: String,
        default: ""
      },
      startPos:{
        type: Object
      },
      endPos:{
        type: Object
      },
      controlPos1:{
        type: Object,
        default: function () {
          return {x:0, y:0}
        }
      },
      controlPos2:{
        type: Object,
        default: function () {
          return {x:0, y:0}
        }
      },
      yOffset:{
        type: Number,
        default: 0
      },
      twoWay:{
        type: Boolean,
        default: false
      },
      flow:{
        type: Array,
        default: function () {
          return []
        }
      }

    },
    data () {
      return {
        /*p1x: this.cpx1,
        p1y: this.cpy1,
        p2x: this.cpx2,
        p2y: this.cpy2,
        slct: this.selected*/
      }
    },
    mounted(){
          /*var p1 = document.getElementById(this.instanceName+'_1');
          p1.onmousedown = e => {
            document.onmousemove = e => {
              this.p1x = e.offsetX;
              this.p1y = e.offsetY;
            };
            document.onmouseup = e => {
              document.onmousemove = null;
              document.onmouseup = null;
            };
          }
          var p2 = document.getElementById(this.instanceName+'_2');
          p2.onmousedown = e => {
            document.onmousemove = e => {
              this.p2x = e.offsetX;
              this.p2y = e.offsetY;
            };
            document.onmouseup = e => {
              document.onmousemove = null;
              document.onmouseup = null;
            };
          }*/
    },
    watch:{
    },
    computed:{
      pathString() {
        let path_str = 'M '+this.startPos.pos.x*100+','+((-this.startPos.pos.y)*100);
        if(this.className == 'BezierPath') {
          path_str += ' C '+this.controlPos1.x*100+','+((-this.controlPos1.y)*100)+
                      ' '+this.controlPos2.x*100+','+((-this.controlPos2.y)*100)+
                      ' '+this.endPos.pos.x*100+','+((-this.endPos.pos.y)*100);
        } else {
          path_str += ' L'+this.endPos.pos.x*100+','+((-this.endPos.pos.y)*100);
        }
        return path_str;
      },
      centerX() {
        if(this.className == 'BezierPath') {
          return 100*(this.startPos.pos.x*0.125+this.controlPos1.x*0.375+this.controlPos2.x*0.375+this.endPos.pos.x*0.125);
        } else {
          return 50*(this.endPos.pos.x+this.startPos.pos.x);
        }
      },
      centerY() {
        if(this.className == 'BezierPath') {
          return -100*(this.startPos.pos.y*0.125+this.controlPos1.y*0.375+this.controlPos2.y*0.375+this.endPos.pos.y*0.125);
        } else {
          return -50*(this.endPos.pos.y+this.startPos.pos.y);
        }
      },
      arrowAngle() {
        let deltaX = 0;
        let deltaY = 0;
        if (this.className == 'BezierPath') {
          let pointX = 100*(this.startPos.pos.x*0.110592+this.controlPos1.x*0.359424+this.controlPos2.x*0.389376+this.endPos.pos.x*0.140608);
          let pointY = -100*(this.startPos.pos.y*0.110592+this.controlPos1.y*0.359424+this.controlPos2.y*0.389376+this.endPos.pos.y*0.140608);
          deltaX = pointX-this.centerX;
          deltaY = pointY-this.centerY;
        } else {
          deltaX = this.endPos.pos.x-this.startPos.pos.x;
          deltaY = this.startPos.pos.y-this.endPos.pos.y;
        }
        let angle = Math.atan(deltaY/deltaX) * 180 / Math.PI;
        if(deltaX < 0) {
          angle += 180;
        }
        return angle;
      },
      arrowString() {
        if(this.twoWay) {
          return 'M '+this.centerX+','+(this.centerY-5)+'l15,5 l-15,5 l-15,-5 z';
        } else {
          return 'M '+this.centerX+','+this.centerY+'l0,-5 l15,5 l-15,5 z';
        }
      },
      flowText() {
        return this.flow.toString();
      }
    }
  }
</script>

<style scoped>
.path_active{
  fill:none;stroke:#777777;stroke-width:6px;
}
.path{
  fill:none;stroke:#999999;stroke-width:4px;
}
.path:hover{
  stroke-width:10px;
}
</style>