<template>
  <g :transform="'translate('+pos.x*100+','+((-pos.y)*100)+')'">
    <g :class="{station_active: active, station: !active}" :style="{cursor:mouseStyle}" @click.stop="$emit('select')" @mousedown="$emit('mouse_down')" @mouseover="$emit('mouse_over')" @contextmenu="$emit('rbclick')">
      <circle :r="16" :class="className" style="stroke-width:2px;stroke:#75923C" />
      <text :y="-30" style="dominant-baseline:middle;text-anchor:middle;" fill="#eeeeee" font-size="28">{{instanceName}}</text>
      <title>{{instanceName}}</title>
    </g>
  </g>
</template>

<script>
  export default {
    components: {},
    props: {
      className:{
        type: String,
        default: ""
      },
      instanceName:{
        type: String,
        default: ""
      },
      pos:{
        type: Object
      },
      dir:{
        type: Number,
      },
      botWidth:{
        type: Number,
        default: 0.73
      },
      botLength:{
        type: Number,
        default: 1.05
      },
      active:{
        type: Boolean,
        default: false
      },
      editMode:{
        type: String,
        default: 'normal'
      },
    },
    data () {
      return {
      }
    },
    computed:{
      bArrow: function() {
        return typeof(this.dir) != 'undefined'
      },
      botRotate: function() {
        console.log(this.bArrow)
        if(this.dir) {
          return 'rotate('+(-this.dir)*180/Math.PI+')';
        } else {
          return '';
        }
      },
      botRidus: function() {
        return Math.sqrt(this.botWidth*this.botWidth+this.botLength*this.botLength)/2;
      },
      arrowPath: function() {
        return 'M'+(this.botLength*50-4)+',0 l-12,-7 l0,14 z';
      },
      mouseStyle() {
        if(this.editMode == 'addStation' || this.editMode == 'addLine') {
          return 'crosshair';
        } else if(this.editMode == 'erase') {
          return 'none';
        }else {
          return 'pointer';
        }
      },
      strokeColor() {
        return this.active? '#F7B902':'#75923C';
      }
    },
    methods: {
      /*rightbtn() {
        console.log("station rightbtn");
        let template=[
          {label:'',click:()=>{console.log('delete')}},
          {label:'',accelerator:'ctrl+c'}
        ];
        let menu = Menu.buildFromTemplate(template);
        menu.popup({});
      }*/
    }
  }
</script>

<style scoped>
.rotate_area {
  stroke: #cccccc; stroke-width: 1px; fill:red;
}
.rotate_area_active {
  stroke: #1296db; stroke-width: 1px; fill:none;
}
.station {
  opacity:0.75;
  /*cursor: pointer;*/
}
.station:hover{
  opacity:1;
}
.station_active{
  opacity:1;
}
.LandMark{
  fill:#a8e233;
}
.ChargePoint{
  fill:#e7583e;
}
.ParkPoint{
  fill:#b944e4;
}
.ActionPoint{
  fill:#4ddfe4;
}
.bot_shape_active{
  stroke: #F7B902; stroke-width: 2px;fill:#D7E4BC;
}
</style>